* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fff;
  background-color: #221e32;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  color: #fafafa;
  background-color: #1300c290;
  border: 2px solid #e1ff00;
  border-radius: 150px 28px;
  outline-color: #df0;
  margin: 20px 0 20px 20px;
  padding: 8px 8px 8px 20px;
  box-shadow: 4px 8px 20px 3px #0038a8cc;
}

#search-box:focus {
  box-shadow: 5px 5px 15px 5px #f2ff01, -9px 5px 15px 5px #d0ff00, -7px -5px 15px 5px #19007d, 12px -5px 15px 5px #1300c2, 12px 10px 15px 7px #19007d, -10px 10px 15px 7px #1300c2, -10px -7px 27px 1px #1900ff, 5px 5px 15px 5px #008eb276;
}

.country-list {
  flex-direction: column;
  gap: 15px;
  list-style: none;
  display: flex;
}

.country__item {
  background: linear-gradient(145deg, #0d013e, #2711ba);
  border: 1px solid #0d013e;
  border-radius: 20px;
  align-items: center;
  gap: 30px;
  padding: 10px;
  display: flex;
  box-shadow: 12px 12px 30px #2e2bc2, -12px -12px 30px #1d06eb;
}

.country__img {
  background-size: contain;
}

.country-wrapper {
  background: linear-gradient(145deg, #0d013e, #2711ba);
  border: 1px solid #0d013e;
  border-radius: 20px;
  margin-left: 20px;
  padding: 30px;
  display: inline-block;
  box-shadow: 12px 12px 30px #2e2bc2, -12px -12px 30px #1d06eb;
}

